import { pages } from "projectConfig"
import React from "react"
import { Link } from "gatsby"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsStaticPage"

export default function RulesPage(props) {
  return (
    <>
      <GatsbySeo
        title={pages.rules.title}
        description={pages.rules.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.rules.title,
          description: pages.rules.description,
        }}
      />
      <Layout>
        <section>
          <article className={"adaptive text"}>
            <h1>Правила</h1>
            <p>
              <b>
                Покупая у нас товар, вы соглашаетесь со следующими правилами:
              </b>
            </p>
            <ol>
              <li>
                Проверка качества товара сразу после покупки. В случае любых
                проблем - сначала ознакомьтесь с{" "}
                <Link to={"/faq"}>ответами на частые вопросы</Link>. Если после
                этого вам все еще нужна будет помощь, пожалуйста, свяжитесь с
                нами по контактам,{" "}
                <Link to={"/contacts"}>указанным на сайте</Link>.
              </li>
              <li>
                Мы проверяем аккаунты приватным софтом, на приватных прокси, что
                гарантирует защиту аккаунтов от бана.
              </li>
              <li>
                Купленные аккаунты необходимо сохранить на своем ПК. В нашем
                магазине Ваши аккаунты хранятся в течение 30 дней, после чего
                ссылка для скачивания удаляется.
              </li>
              <li>Незнание правил не освобождает от ответственности.</li>
              <li>
                В случае обращения к нам за помощью, ответ поступает минимум в
                течение 10 минут, очень редко в течение дня.
              </li>
              <li>
                Внимание! Совершая покупку в нашем магазине, вы автоматически
                соглашаетесь и принимаете данные условия. Если Вы не согласны с
                данными условиями - воздержитесь от покупки.
              </li>
            </ol>
          </article>
        </section>
      </Layout>
    </>
  )
}
